@import '../../../assets/css/base';


.head {
  display: flex;
  justify-content: center;
  margin-top: 60px;
  margin-bottom: 83px;
}

.head-none {
  display: none;
}

.cloudy {
  width: 114px;
  background-image: url("../../../assets/images/weather-icon-cloudy.png");
}

.rainy {
  width: 100px;
  background-image: url("../../../assets/images/weather-icon-rainy.png");
}

.sunny {
  width: 88px;
  background-image: url("../../../assets/images/weather-icon-sunny.png");
}

.icon {
  margin-right: 30px;
  background-size: contain;
  background-repeat: no-repeat;
  height: 88px;
}

.current-date {
  display: flex;
  flex-direction: column;
}

.current-date p {
  font-family: 'Roboto', sans-serif;
  font-weight: 400;
  font-size: 28px;
  color: $color1;
  display: inline-block;
  margin-top: 15px;
  margin-bottom: 10px;
}

.current-date span {
  font-family: 'Roboto', sans-serif;
  font-weight: 200;
  font-size: 14px;
  color: $color1;
  text-transform: uppercase;
}

.current-weather {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 38px;
}

.current-month {
  display: flex;
  align-items: center;
  margin-bottom: 38px;
  & > span:first-child {
    margin-right: 10px;
  }
}

.current-weather .temperature {
  position: relative;
  font-family: 'Roboto', sans-serif;
  font-weight: 200;
  font-size: 350px;
  line-height: 1;
  color: $color1;
  margin-bottom: 100px;
}

.current-weather .temperature::after {
  position: absolute;
  top: 53px;
  right: -70px;
  content: '';
  display: inline-block;
  width: 37px;
  height: 41px;
  border: solid 5px $color1;
  border-radius: 50%;
}

.current-weather .meta span {
  font-family: 'Roboto', sans-serif;
  font-weight: 200;
  font-size: 19px;
  color: $color1;
  display: inline-flex;
  align-items: flex-start;
}

.current-weather .meta span:first-child {
  margin-right: 81px;
}

.current-weather .meta .rainy::before {
  content: '';
  display: inline-block;
  width: 26px;
  height: 26px;
  background-image: url("../../../assets/images/rainy.png");
  margin-right: 11px;
}

.current-weather .meta .humidity::before {
  content: '';
  display: inline-block;
  width: 19px;
  height: 28px;
  background-image: url("../../../assets/images/humidity.png");
  margin-right: 11px;
}